
@charset "UTF-8";
#balance_sheet .el-row {
  line-height: 36px;
  margin-bottom: 15px;
}
#balance_sheet .el-row .vl-top-left {
    float: left;
}
#balance_sheet .el-row .vl-top-left .vl-span {
      float: left;
      margin-right: 10px;
}
#balance_sheet .el-row .vl-top-right {
    float: right;
}
#balance_sheet .sheet-table {
  background-color: #fff;
  color: #555;
  font-family: tahoma, arial, 宋体;
  font-size: 12px;
  border-collapse: collapse;
  /*相邻边被合并;separate: 边框独立*/
  border: none;
  width: 100%;
  /* td 内容太多，想自动换行 */
  table-layout: fixed;
}
#balance_sheet .sheet-table tbody tr:nth-child(odd) {
    background-color: #fff;
}
#balance_sheet .sheet-table tbody tr:nth-child(even) {
    background-color: #f8f8f8;
}
#balance_sheet .sheet-table tbody tr:hover {
    background-color: #feee96;
}
#balance_sheet .sheet-table thead {
    background-color: #eee;
}
#balance_sheet .sheet-table thead tr td {
      font-weight: bold;
      text-align: center;
}
#balance_sheet .sheet-table td {
    font-weight: 400;
    overflow: hidden;
    white-space: pre;
    height: 34px;
    padding: 0 5px;
    border: solid #CCC 1px;
    /* td 内容太多，想自动换行 */
    word-wrap: break-word;
}
#balance_sheet .sheet-table td button span {
      color: #555;
      cursor: pointer;
      outline: none;
}
#balance_sheet .sheet-table td button span:hover {
        text-decoration: underline;
}
#balance_sheet .el-dialog--small {
  width: 800px;
}
#balance_sheet .sheet-dialog {
  padding: 10px 5px 20px 5px;
}
#balance_sheet .sheet-dialog .operator-box {
    padding-left: 5px;
    padding-right: 5px;
}
#balance_sheet .sheet-dialog .operator {
    width: 70px;
    height: 30px;
}
#balance_sheet .sheet-dialog .fetch {
    width: 140px;
    height: 30px;
}



