
#change .change-top {
    width: 100%;
    height: 45px;
    color: #555;
}
#change .change-top .change-span {
    float: left;
    line-height: 28px;
}
#change .change-top .change-top-right {
    padding-right: 20px;
    float: right;
}
#change .el-dialog--small {
    width: 500px;
}
#change .el-dialog__body {
    padding: 20px 10px 0px 10px;
}
#change .selectWay {
    text-align: center;
    padding-bottom: 10px;
    margin-bottom: 20px;
    border-bottom: 1px solid #d7dcde;
}
#change .el-input--small {
    width: 215px;
}



