
@charset "UTF-8";
#WageForms .WageForms-top {
  width: 100%;
  height: 45px;
  color: #555;
}
#WageForms .WageForms-top .WageForms-span {
    line-height: 30px;
    float: left;
}
#WageForms .WageForms-top .WageForms-periods {
    float: left;
}
#WageForms .WageForms-top .WageForms-upload {
    float: left;
}
#WageForms .WageForms-top .uploadMb {
    color: #555;
    -webkit-text-decoration-line: none;
            text-decoration-line: none;
    padding-right: 20px;
}
#WageForms .WageForms-top .uploadMb:hover {
      text-decoration: underline;
      color: #0087be;
      cursor: pointer;
}
#WageForms .WageForms-top .WageForms-top-left .el-input__icon + .el-input__inner,
  #WageForms .WageForms-top .WageForms-top-left .el-select .el-input__inner {
    padding-right: 0px;
}
.WageForms-periods {
  margin: 0 10px;
}
#WageForms .WageForms-top .WageForms-top-right {
  padding-right: 20px;
  float: right;
}
#WageForms .WageForms-top .WageForms-top-right .el-button {
    width: 70px;
}
#WageForms .el-dialog--small {
  width: 800px;
}
#WageForms .upload-demo {
  float: left;
  margin-right: 20px;
}

