
@charset "UTF-8";
#profit .el-row {
  line-height: 36px;
  margin-bottom: 15px;
}
#profit .el-row .vl-top-left {
    float: left;
}
#profit .el-row .vl-top-left .forms {
      width: 90px;
      height: 30px;
}
#profit .el-row .vl-top-left .preiod {
      width: 140px;
      height: 30px;
}
#profit .el-row .vl-top-left .profit-check {
      margin-left: 25px;
}
#profit .el-row .vl-top-left .profit-check .el-checkbox__inner {
        width: 15px;
        height: 15px;
}
#profit .el-row .vl-top-left .profit-check .el-checkbox__inner::after {
        -webkit-box-sizing: content-box;
                box-sizing: content-box;
        content: "";
        border: 2px solid #fff;
        border-left: 0;
        border-top: 0;
        height: 6px;
        left: 4px;
}
#profit .el-row .vl-top-left .profit-check .el-checkbox__label {
        font-size: 12px;
        padding-left: 5px;
}
#profit .el-row .vl-top-left .vl-span {
      float: left;
      margin-right: 10px;
}
#profit .el-row .vl-top-right {
    float: right;
}
#profit .profit-table {
  background-color: #fff;
  color: #555;
  font-family: tahoma, arial, 宋体;
  font-size: 12px;
  border-collapse: collapse;
  /*相邻边被合并;separate: 边框独立*/
  border: none;
  width: 100%;
  /* td 内容太多，想自动换行 */
  table-layout: fixed;
}
#profit .profit-table tbody tr:nth-child(odd) {
    background-color: #fff;
}
#profit .profit-table tbody tr:nth-child(even) {
    background-color: #f8f8f8;
}
#profit .profit-table tbody tr:hover {
    background-color: #feee96;
}
#profit .profit-table thead {
    background-color: #eee;
}
#profit .profit-table thead tr td {
      font-weight: bold;
      text-align: center;
}
#profit .profit-table td {
    font-weight: 400;
    overflow: hidden;
    white-space: pre;
    height: 34px;
    padding: 0 5px;
    border: solid #CCC 1px;
    /* td 内容太多，想自动换行 */
    word-wrap: break-word;
}
#profit .profit-table td button span {
      color: #555;
      cursor: pointer;
      outline: none;
}
#profit .profit-table td button span:hover {
        text-decoration: underline;
}
#profit .el-dialog--small {
  width: 800px;
}
#profit .sheet-dialog {
  padding: 10px 5px 20px 5px;
}
#profit .sheet-dialog .operator-box {
    padding-left: 5px;
    padding-right: 5px;
}
#profit .sheet-dialog .operator {
    width: 70px;
    height: 30px;
}
#profit .sheet-dialog .fetch {
    width: 140px;
    height: 30px;
}



