
* {
  padding: 0;
  margin: 0;
  font-family: "Microsoft YaHei", monospace, helvetica, arial, sans-serif;
}
html,
body {
  min-width: 800px;
  min-height: 600px;
  width: 100vw;
  height: 100vh;
}
.after:after {
  content: "";
  display: block;
  height: 0;
  clear: both;
  visibility: hidden;
}
.before:before {
  content: "";
  display: table;
}
[v-cloak] {
  display: none;
}
.refreshfade-enter-active,
.refreshfade-leave-active {
  -webkit-transition: opacity 1s;
  transition: opacity 1s;
}
.refreshfade-enter,
.refreshfade-leave-to {
  opacity: 0;
}
.changefade-enter-active,
.changefade-leave-active {
  -webkit-transition: opacity .2s;
  transition: opacity .2s;
}
.changefade-enter,
.changefade-leave-to {
  opacity: 0;
}
.fz1 {
  font-size: 20px;
}
.fz2 {
  font-size: 18px;
}
.fz3 {
  font-size: 16px;
}
.fz4 {
  font-size: 14px;
}
.fz5, .el-tree, .el-table tbody {
  font-size: 13px;
}
.fz6 {
  font-size: 12px;
}
.custom-primary {
  background-color: #0087be;
}
.custom-border-primary {
  border-color: #0087be;
}
.custom-color-primary {
  color: #0087be;
}
.el-card--custom {
  background-color: white;
  padding: 24px;
  border: 1px solid #d1dde5;
  color: #1f313d;
}
.el-button + .el-button {
  margin-left: 0;
}
table .el-button + .el-button {
  margin-left: 10px;
}
.treeBox {
  border: 1px solid #d1dde5;
}
.el-tree {
  float: left;
  min-width: 100%;
}
.el-tree .el-tree-node__label {
    padding-right: 10px;
}
.el-tree .el-tree-node > .el-tree-node__children {
    overflow: visible;
}
.el-autocomplete-suggestion li:active {
  background-color: #0087be;
  color: white;
}
.el-table {
  border-right: 0;
  border-bottom: 0;
}
.el-table .cell,
  .el-table th > div {
    padding: 0 8px;
}
.el-dialog .el-form {
  width: 440px;
}
.el-dialog .el-form .el-select {
    width: 100%;
}
.aLinkStyle:hover {
  text-decoration: underline;
  cursor: pointer;
}



