
#addsub .addsub-top {
    width: 100%;
    height: 45px;
    color: #555;
}
.addsub-span {
    float: left;
    line-height: 28px;
}
#addsub .addsub-top .addsub-top-right {
    padding-right: 20px;
    float: right;
}



/* 弹出框css */


/* 区间选择 */
#addsub .el-dialog--small {
    width: 400px;
}
#addsub .el-dialog__body {
    padding: 20px 10px 0px 10px;
}
#addsub .dialog .selectWay {
    text-align: center;
    padding-bottom: 10px;
    margin-bottom: 20px;
    border-bottom: 1px solid #d7dcde;
}
#addsub .el-input--small {
    width: 215px;
}



/* 下拉的类别列表 */
.dropdownSelect .el-scrollbar {
    width: 215px;
    overflow: hidden;
}



