
@charset "UTF-8";
#proItemized .proItemized-top {
  color: #555;
  width: 100%;
  height: 45px;
}
#proItemized .proItemized-top .proItemized-span {
    float: left;
}
.assist-type {
  float: left;
}
#proItemized .proItemized-top .accout-proItemized,
#proItemized .proItemized-top .proItemized-upload {
  float: left;
}
#proItemized .proItemized-top .proItemized-span {
  line-height: 28px;
}
#proItemized .proItemized-top .assist-type,
#proItemized .proItemized-top .accout-proItemized {
  margin: 0 10px;
}
#proItemized .proItemized-top-left .assist-type .el-input--small .el-input__inner {
  height: 28px;
  border-radius: 0px;
  width: 100px;
}
#proItemized .ui-btn-menu {
  vertical-align: middle;
  zoom: 1;
  z-index: 3;
}
#proItemized .ui-btn-menu-cur .menu-btn:active, #proItemized .ui-btn-menu-cur .menu-btn:hover {
  padding-bottom: 1px;
  background: #fff !important;
  border-bottom: none;
  border-bottom-left-radius: 0;
  border-bottom-right-radius: 0;
  -webkit-box-shadow: none;
          box-shadow: none;
}
#proItemized .ui-btn-menu .menu-btn {
  position: relative;
  margin-right: 0;
  padding-right: 20px;
  z-index: 3;
}
#proItemized .ui-btn-menu-cur .ui-btn {
  border-bottom: 1px solid #fff;
  background-color: #fff;
}
#proItemized .ui-btn {
  display: inline-block;
  padding: 0 6px;
  height: 26px;
  line-height: 26px;
  font-size: 12px;
  line-height: 26px;
  border: 1px solid #bfcfd9;
  border-radius: 2px;
  background: #fff;
  color: #555;
  vertical-align: middle;
  cursor: pointer;
}
#proItemized .ui-btn-menu .menu-btn b {
  font-size: 0;
  position: absolute;
  right: 4px;
  top: 50%;
  margin-top: -2px;
  border-width: 6px;
  border-color: #bfcfd9 #fff #fff;
  border-style: solid;
  height: 0;
  overflow: hidden;
  -webkit-transition: -webkit-transform 150ms ease-in;
  transition: -webkit-transform 150ms ease-in;
  transition: transform 150ms ease-in;
  transition: transform 150ms ease-in, -webkit-transform 150ms ease-in;
}
#proItemized .ui-btn-menu-cur .menu-btn b {
  -moz-transform: rotate(180deg);
  -moz-transform-origin: 50% 30%;
  -webkit-transform: rotate(180deg);
  -webkit-transform-origin: 50% 30%;
  -o-transform: rotate(180deg);
  -o-transform-origin: 50% 20%;
  transform: rotate(180deg);
  transform-origin: 50% 30%;
  filter: progid:DXImageTransform.Microsoft.BasicImage(rotation=2);
}
#proItemized .proItemized-top-left .con {
  width: 360px;
  padding: 15px;
  position: absolute;
  top: 27px;
  left: 108px;
  border: 1px solid #c1c1c1;
  border-bottom-left-radius: 2px;
  border-bottom-right-radius: 2px;
  -webkit-box-shadow: 0 1px 1px rgba(0, 0, 0, 0.15);
          box-shadow: 0 1px 1px rgba(0, 0, 0, 0.15);
  background-color: #fff;
  z-index: 2;
}
#proItemized .proItemized-top-left .con ul li {
    padding-bottom: 20px;
    list-style-type: none;
    font-size: 14px;
    text-align: -webkit-match-parent;
    color: #555;
}
#proItemized .proItemized-top-left .con .el-input--small .el-input__inner {
    height: 30px;
    width: 112px;
}
.proItemized-top-left .con .el-input__icon {
  right: -4px;
}

/* 科目那个input */
#proItemized .proItemized-top-left .con ul .subject .el-input__inner {
  width: 246px;
}
#proItemized .proItemized-top .proItemized-top-left .el-input__icon + .el-input__inner,
#proItemized .proItemized-top .proItemized-top-left .el-select .el-input__inner {
  padding-right: 0px;
}
#proItemized .proItemized-top-right {
  display: block;
  padding-right: 20px;
  float: right;
  font-size: 13px;
}
#proItemized .proItemized-top-right .el-button {
    width: 70px;
}
#proItemized .l-layout-header {
  cursor: default;
  padding-bottom: 10px;
  font-size: 15px;
  height: 24px;
  line-height: 24px;
  font-weight: 400;
  padding-left: 10px;
  overflow: hidden;
}
#proItemized .subject-shortcut .shortcut-div {
  height: 40px;
  padding-left: 15px;
  line-height: 40px;
  background-color: #eef2f6;
}
#proItemized .subject-shortcut .shortcut-div .el-input__inner {
    height: 32px;
}
#proItemized .subject-shortcut .subject-div {
  background-color: #fff;
  height: 100%;
}



