    line-height: 28px;
    margin-right: 15px;
}
#sub .sub-top .sub-upload {
    margin-left: 10px;
}
#sub .ui-btn-menu {
  vertical-align: middle;
  zoom: 1;
  z-index: 3;
}
#sub .ui-btn-menu-cur .menu-btn:active, #sub .ui-btn-menu-cur .menu-btn:hover {
  padding-bottom: 1px;
  background: #fff !important;
  border-bottom: none;
  border-bottom-left-radius: 0;
  border-bottom-right-radius: 0;
  -webkit-box-shadow: none;
          box-shadow: none;
}
#sub .ui-btn-menu .menu-btn {
  position: relative;
  margin-right: 0;
  padding-right: 20px;
  z-index: 3;
}
#sub .ui-btn-menu-cur .ui-btn {
  border-bottom: 1px solid #fff;
  background-color: #fff;
}
#sub .ui-btn {
  display: inline-block;
  padding: 0 6px;
  height: 26px;
  line-height: 26px;
  font-size: 12px;
  border: 1px solid #bfcfd9;
  border-radius: 2px;
  background: #fff;
  color: #555;
  vertical-align: middle;
  cursor: pointer;
}
#sub .ui-btn-menu .menu-btn b {
  font-size: 0;
  position: absolute;
  right: 4px;
  top: 50%;
  margin-top: -2px;
  border-width: 6px;
  border-color: #bfcfd9 #fff #fff;
  border-style: solid;
  height: 0;
  overflow: hidden;
  -webkit-transition: -webkit-transform 150ms ease-in;
  transition: -webkit-transform 150ms ease-in;
  transition: transform 150ms ease-in;
  transition: transform 150ms ease-in, -webkit-transform 150ms ease-in;
}
#sub .ui-btn-menu-cur .menu-btn b {
  -moz-transform: rotate(180deg);
  -moz-transform-origin: 50% 30%;
  -webkit-transform: rotate(180deg);
  -webkit-transform-origin: 50% 30%;
  -o-transform: rotate(180deg);
  -o-transform-origin: 50% 20%;
  transform: rotate(180deg);
  transform-origin: 50% 30%;
  filter: progid:DXImageTransform.Microsoft.BasicImage(rotation=2);
}
