[v-cloak] {
    display: none
}


/* 数据渲染是的闪烁问题 */
#colAccount .colAccount-top {
    color: #555;
    width: 100%;
    height: 45px;
}
.colAccount-span,
.accout-colAccount,
.colAccount-upload {
    float: left;
}
.colAccount-span {
    line-height: 28px;
}
.accout-colAccount{
    margin: 0 10px;
}

/* 明细账选择菜单卡片效果 公共样式*/
.ui-btn-menu {
    vertical-align: middle;
    zoom: 1;
    z-index: 3;
}
.ui-btn-menu-cur .menu-btn:active,
.ui-btn-menu-cur .menu-btn:hover {
    padding-bottom: 1px;
    background: #fff!important;
    border-bottom: none;
    border-bottom-left-radius: 0;
    border-bottom-right-radius: 0;
    -webkit-box-shadow: none;
            box-shadow: none;
}
.ui-btn-menu .menu-btn {
    position: relative;
    margin-right: 0;
    padding-right: 20px;
    z-index: 3;
}
.ui-btn-menu-cur .ui-btn {
    border-bottom: 1px solid #fff;
    background-color: #fff;
}
.ui-btn {
    display: inline-block;
    padding: 0 6px;
    height: 26px;
    line-height: 26px;
    font-size: 12px;
    line-height: 26px;
    border: 1px solid rgb(191, 207, 217);
    border-radius: 2px;
    background: #fff;
    color: #555;
    vertical-align: middle;
    cursor: pointer;
}
.ui-btn-menu .menu-btn b {
    font-size: 0;
    position: absolute;
    right: 4px;
    top: 50%;
    margin-top: -2px;
    border-width: 6px;
    border-color: #bfcfd9 #fff #fff;
    border-style: solid;
    height: 0;
    overflow: hidden;
    -webkit-transition: -webkit-transform 150ms ease-in;
    transition: -webkit-transform 150ms ease-in;
    transition: transform 150ms ease-in;
    transition: transform 150ms ease-in, -webkit-transform 150ms ease-in;
}



/* hover之后三角到过来 */
.ui-btn-menu-cur .menu-btn b {
    -moz-transform: rotate(180deg);
    -moz-transform-origin: 50% 30%;
    -webkit-transform: rotate(180deg);
    -webkit-transform-origin: 50% 30%;
    -o-transform: rotate(180deg);
    -o-transform-origin: 50% 20%;
    transform: rotate(180deg);
    transform-origin: 50% 30%;
    filter: progid:DXImageTransform.Microsoft.BasicImage(rotation=2);
}


/* 明细账选择菜单卡片效果 公共样式*/
.colAccount-top-left .con {
    width: 360px;
    padding: 15px;
    position: absolute;
    top: 27px;
    left: 52px;
    border: 1px solid #c1c1c1;
    border-bottom-left-radius: 2px;
    border-bottom-right-radius: 2px;
    -webkit-box-shadow: 0 1px 1px rgba(0, 0, 0, .15);
            box-shadow: 0 1px 1px rgba(0, 0, 0, .15);
    background-color: #fff;
    z-index: 2;
}


/* 区间选择 */
.colAccount-top-left .con ul li {
    padding-bottom: 20px;
    list-style-type: none;
    font-size: 14px;
    text-align: -webkit-match-parent;
    color: #555;
}
#colAccount .colAccount-top-left .con .el-input--small .el-input__inner {
    height: 30px;
    width: 112px;
}
.colAccount-top-left .con .el-input__icon {
    right: -4px;
}


/* 科目那个input */
#colAccount .colAccount-top-left .con ul .subject .el-input__inner {
    width: 246px;
}


/* 科目那个input */


/* hover之后自动追加.ui-btn-menu-cur */


/* 明细账选择菜单卡片效果 */
#colAccount .colAccount-top  .colAccount-top-left .el-input__icon+.el-input__inner {
    padding-right: 0px;
}
#colAccount .colAccount-top .colAccount-top-left .el-select .el-input__inner {
    padding-right: 0px;
}


/* 右边按钮部分 */
.colAccount-top-right {
    display: block;
    padding-right: 20px;
    float: right;
    font-size: 13px;
}
.colAccount-top-right .el-button {
    width: 70px;
}


