
#wagePro .wagePro-top {
  color: #555;
  width: 100%;
  height: 45px;
}
#wagePro .wagePro-top .add-input {
    width: 250px;
    margin-right: 20px;
    float: left;
}
#wagePro .wagePro-top .add-btn {
    float: left;
}
#wagePro .wagePro-content .cell .startUse {
  color: #fff;
  border: none;
  background-color: #339fcb;
}
#wagePro .wagePro-content .cell .startUse:hover {
    background: #339fcb;
    border: none;
    color: #fff;
}
#wagePro .wagePro-content .cell .stopUse {
  color: #fff;
  border: none;
  background-color: #aaa;
}
#wagePro .wagePro-content .cell .stopUse:hover {
    background: #aaa;
    border: none;
    color: #fff;
}



